body {
  margin: 0;
}

body,
button,
input,
select,
textarea {
  font-family: "Source Sans Pro", "Helvetica", "Arial", sans-serif;
  font-size: 18px;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  clear: both;
}

p {
  margin-bottom: 1.5em;
}

b,
strong {
  font-weight: bold;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5em;
}

address {
  margin: 0 0 1.5em;
}

pre {
  display: inline-block;
  font-family: "Courier 10 Pitch", Courier, monospace;
  color: #ddca7e;
  background: #282c34;
  padding: 0.5em 0.8em;
  border-radius: 0.2em;
}

code,
kbd,
tt,
var {
  font: 15px Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark,
ins {
  background: #fff9c0;
  text-decoration: none;
}

sup,
sub {
  font-size: 75%;
  height: 0;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  bottom: 1ex;
}

sub {
  top: 0.5ex;
}

small {
  font-size: 75%;
}

big {
  font-size: 125%;
}

/* Global layouts */
@media screen and (min-width: 700px) {
  .two-column {
    column-count: 2;
    column-gap: 2.4em;
  }
}

/* Global buttons */
.content-button {
  padding: 0.5em 1em;
  border: 3px solid #b51c35;
  /*border-radius: 10px;*/
  font-weight: 600;
}

a.content-button {
  color: black;
  text-decoration: none;
}

a.content-button:focus,
a.content-button:hover {
  background: #b51c35;
  color: #fffced;
}

/*--------------------------------------------------------------
General styling
--------------------------------------------------------------*/

h1,
h2,
h3 {
  text-align: center;
  margin: 0;
  padding: 1em 0;
}

p {
  margin: 0;
  padding: 1em 0;
}

img {
  display: block;
  border: 0;
  width: 100%;
  height: auto;
}

/* Promo section */

.logo,
.site-title {
  text-align: center;
}

.logo {
  font-size: 6em;
}

.site-title {
  margin-top: -1em;
  margin-bottom: 3em;
}

/* Splash section */

.splash {
  background: #fff6e5;
  padding-bottom: 2em;
}

.splash-content {
  padding: 1.5em;
}

@media screen and (min-width: 600px) {
  .splash-text {
    columns: 2;
    column-gap: 2em;
  }

  .splash-text p {
    padding-top: 0;
  }
}

/* Buckets section */

.buckets {
  padding: 2em 1em 1em;
  background: #3e454c;
}

.buckets ul {
  margin: 0;
  padding: 0;
}

.buckets li {
  margin-bottom: 1em;
  background: white;
  list-style-type: none;
}

.bucket {
  padding: 1.5em;
}

/* More section */

.more {
  padding: 2em;
}

/* Twins section */

.twin {
  padding: 2em;
  background: #2185c5;
  color: white;
}

.twin:last-of-type {
  background: #ff7f66;
}

/* Colophon section */
.colophon {
  padding: 2em;
  background: hsl(0, 0%, 10%);
  color: white;
  text-align: center;
}

.colophon a {
  color: white;
}

.colophon a:hover,
.colophon a:focus {
  border-bottom: 1px solid white;
}

/*--------------------------------------------------------------
Use flex to create a three-bucket layout
--------------------------------------------------------------*/

@media screen and (min-width: 700px) {
  @supports (display: flex) {
    .buckets ul {
      display: flex;
      justify-content: space-around;
    }

    .buckets li {
      width: 31%;
    }
  }
}

/*--------------------------------------------------------------
Basic responsive layout for all browsers:
--------------------------------------------------------------*/

.grid {
  max-width: 50em;
  margin: 0 auto;
}

/*--------------------------------------------------------------
CSS Grid layout for wider screens, when browser supports grid:
--------------------------------------------------------------*/

@media screen and (min-width: 600px) {
  /* Layout with CSS Grid */
  @supports (grid-area: auto) {
    /* Four-column layout. Two center columns have a total max width of 50em: */
    .grid {
      max-width: none;
      /*margin: 0;*/
      display: grid;
      grid-template-columns: 1fr repeat(2, minmax(auto, 25em)) 1fr;
    }

    /* Center items by placing them in the two center columns: */
    .splash-content,
    .more-content,
    .buckets ul {
      grid-column: 2/4;
    }

    /* Use automatic grid placement + span to let each item span two columns:*/
    .twin,
    .colophon aside {
      grid-column: span 2;
    }
  }
}
